@import "../const.sass";

.blockWrap{
  padding: 48px 0;
  &:nth-child(even){
    background: $block-bg-blue;
  }

  &:nth-child(odd){
    background: $block-bg-white;
  }

  .ant-row-flex{
    margin-top: 20px;
  }

  :global(.ant-card-meta-description){
    display: -webkit-box;
    min-height: 64px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
}

.fixWrap{
  position: fixed;
  right: 4%;
  bottom: 30%;
  width: 130px;
  img{
    width: 100%;
  }
  :global(.ant-card){
    border-radius: 10px;
  }
  :global(.ant-card-body){
    padding: 0px 14px 14px;
  }
  .fixBody{
    margin-top: 4px;
    font-size: 14px;
  }
}
